import React, { useState } from 'react'
import styled from 'styled-components'
import { menuData } from '../data/MenuData'
import { Container } from '../globalStyle'
import Logo from '../img/logo.svg'

const MenuBars = styled.div`
    position: relative;
    display: none;
    flex-direction: column;
    align-items: center;
    @media(max-width: 992px) {
        display: flex;
        flex: 0 0 18px
    }

    span {
        display: block;
        height: 2px;
        width: 100%;
        background: #fff;
        transition: transform 1s cubic-bezier(.23,1,.32,1);
        
        &:not(:last-child) {
            margin-bottom: 8px;
        }
        &:first-child {
            transform: ${({ isOpen }) => isOpen ? 'rotate(-45deg) translateY(7px);' : ''};
        }
        &:last-child {
            transform: ${({ isOpen }) => isOpen ? 'rotate(45deg) translateY(-7px);' : ''};
        }
    }
`

const LanguageItems = styled.div`
    position: absolute;
    top: -50%;
    left: 50%;
    transform: ${({ isLanguage }) => isLanguage.state ?  'translateY(42%) translateX(-50%)' : 'translateY(-180%) translateX(-50%)'};
    background: #fff;
    padding: 12px 13px 12px 11px;
    width: 48px;
    display: flex;
    flex-direction: column;
    transition: transform .2s ease-in-out;
    z-index: 3;
`

const Navbar = (props) => {

    const [isLanguage, setIsLanguage] = useState({
        state: false,
        currentLanguage: 'Укр',
        languages: ['Рус', 'Анг']
    });

    const changeLanguage = lang => {
        const fallbackLang = isLanguage.languages.map(item => {
            if(item === lang) {
                item = isLanguage.currentLanguage
            }
            return item
        })
        const newCurrentLanguage = lang
        // let newLanguage

        // switch (lang) {
        //     case 'Укр': newLanguage = 'ua'
        //         break;
        //     case 'Рус': newLanguage = 'ru'
        //         break;
        //     default: newLanguage = 'ua'
        //         break;
        // }

        setIsLanguage({...isLanguage, currentLanguage: newCurrentLanguage, languages:fallbackLang })
    }

    return (
        <div className="navbar">
            <Container>
                <div className="navbar__wrapper">
                    <MenuBars onClick={() => props.menuHandler(!props.menuStatus)} isOpen={props.menuStatus}>
                        <span></span>
                        <span></span>
                    </MenuBars>
                    <div className="logo">
                        <img src={Logo} alt="swim system"/>
                    </div>
                    <div className="desktop-controls">
                        <div className="navbar__menu">
                        {
                            menuData.map(item => (
                                <a key={item.id} href={item.anchor} className="_text-on-load navbar__menu-link">
                                    {item.title}
                                </a>
                            ))
                        }
                        </div>
                        <div 
                            className="lang-toggle"
                            onMouseEnter={() => setIsLanguage({...isLanguage, state: true})}
                            onMouseLeave={() => setIsLanguage({...isLanguage, state: false})}
                        >
                            <div className="lang-toggle__current _text-on-load">{isLanguage.currentLanguage}</div>
                            <LanguageItems isLanguage={isLanguage}>
                                {isLanguage.languages.map((lang, i) => <button className="lang-toggle__item" onClick={() => changeLanguage(lang)} key={i}>{lang}</button>)}
                            </LanguageItems>
                        </div>
                    </div>            
                </div>
            </Container>
        </div>
    )
}

export default Navbar